a:link {
  color: white;
}

/* visited link */
a:visited {
  color: whitesmoke;
}

/* mouse over link */
a:hover {
  transition: 500ms;
  color: black;
}

/* selected link */
a:active {
  color: white;
}

.three:hover {
  opacity: 0.8;
  transition: 300ms;
  font-size: 3.5rem;
}
